.home {
  background: url('../../assets/C911_Pick3.JPG');
	margin: 0;
	width: 100vw;
	height: calc(100vh - 107.75px);
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	background-size: cover;
	background-repeat: no-repeat;
	position: relative;
	background-position: center 0;
}

.home-text {
	text-align: center;
	color: white;
	font-weight: bold;
	text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000,
		1px 1px 0 #000;
	font-size: 3.5rem;
	width: 100%;
}

